* {
  box-sizing: border-box;
  color: var(--clr-font);
}

:root {
  --clr-default: rgb(0,110,100);
  --clr-bg: rgb(200, 215, 200);
  --clr-font: rgb(30,40,30);
}

.App {
  text-align: center;
}

#body-box {
  background-color: var(--clr-bg);
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
}

#editor-box {
  border-radius: 4px;
  height: 100vh;
  width: 30vw;
  background-color: var(--clr-bg);
}

#editor-content {
  width: 100%;
  height: 100vh;
  padding: 2px;
}

#preview-box {
  height: auto;
  width: 70vw;
  background-color: var(--clr-bg);
}

#preview-content {
  width: auto;
  height: 100vh;
  padding: 2px;
}

.header {
  border-radius: 4px 4px 0 0 ;
  width: 100%;
  height: auto;
  background-color: rgb(0,110,100);
  display: flex;
}

#editor {
  border: none;
  padding: 20px;
  border-radius: 0 0 4px 4px;
  min-width: 100%;
  max-width: 100%;
  height: 95vh;
  overflow: auto;
}

.header-heading {
  margin: auto;
  color: rgb(240, 240, 240); 
}

#preview {
  height: 92vh;
  overflow-y: scroll;
  padding: 20px;
}